<template>
  <div class="river-house">
    <!-- Q3 -->
    <div v-if="step===3" class="house-0-box">
      <div class="house-0-talk-box">
        <div style="background: none; margin-top: 20vh;" class="house-0-frame">
          <div style="color: white;">你聽到一陣流水聲，眼前憑空出現了一個看起來十來歲，穿著日式服裝的小孩。他似乎很慌張的樣子，低著頭碎碎唸著什麼。</div>
          <div @click="goToStepQ31" class="house-0-triangle-box">
            <div style="border-color: white transparent transparent transparent;" class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q3-1 -->
    <div v-if="step===3.1" class="house-0-box">
      <img class="house-0-fishbird" src="@/assets/images/river/river-black-icon.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">？？？</div>
        <div class="house-0-frame">
          <div>我是誰？我在哪裡？我要做什麼？咦？有人⋯⋯啊啊啊啊啊！有怪奇啊！救命啊、救命啊！」</div>
          <div @click="goToStepQ315" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q3-1-5 -->
    <div v-if="step===3.15" class="house-0-box">
      <img class="house-0-fishbird" src="@/assets/images/river/river-black-icon.png" alt="ghost">
      <div class="house-0-all-box">
        <div @click="goToStepQ4" class="house-0-all-btn">我是人類，你才是怪奇吧⋯⋯</div>
        <div v-if="isShowQ315" @click="goToStepQ32" class="house-0-all-btn">嘿嘿嘿，我就是怪～奇，小朋友～你乖乖跟我走吧</div>
      </div>
    </div>
    <!-- Q3-2 -->
    <div v-if="step===3.2" class="house-0-box">
      <div class="house-0-talk-box">
        <div style="background: none; margin-top: 20vh;" class="house-0-frame">
          <div style="color: white;">你說的話被買咖啡而路過的警察聽到了，因為被當成是誘拐兒童的嫌犯，而被帶回了警局。你費了好一番力氣才逃脫。</div>
          <div @click="goToStepQ3" class="house-0-triangle-box">
            <div style="border-color: white transparent transparent transparent;" class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q4 -->
    <div v-if="step===4" class="house-0-box">
      <img class="house-0-fishbird" src="@/assets/images/river/river-black-icon.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">？？？</div>
        <div class="house-0-frame">
          <div>我是怪奇⋯⋯你是人類⋯⋯啊！館長叔叔說，怪奇壞，人類好，所以你是好人，我是壞孩子⋯⋯（哭泣）嗚嗚嗚，原來我是壞孩子，所以大家都不要我了⋯⋯嗚嗚嗚，我乾脆去死好了。我不想活了，你快帶我回怪奇博物館吧，嗚嗚嗚。</div>
          <div @click="goToStepQ45" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q4-5 -->
    <div v-if="step===4.5" class="house-0-box">
      <img class="house-0-fishbird" src="@/assets/images/river/river-black-icon.png" alt="ghost">
      <div class="house-0-all-box">
        <div @click="goToStepQ99" class="house-0-all-btn">既然你這麼說了，那就跟我走吧。（不疑有他）</div>
        <div @click="goToStepQ5" class="house-0-all-btn">等一下，為什麼你說得好像回去博物館就會死掉一樣啊？</div>
      </div>
    </div>
    <!-- Q5 -->
    <div v-if="step===5" class="house-0-box">
      <img class="house-0-fishbird" src="@/assets/images/river/river-black-icon.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">？？？</div>
        <div class="house-0-frame">
          <div>帶我回去、帶我回去，你還在等什麼？為什麼不帶我回去，你是不是不想帶我回去？可惡可惡可惡，為什麼不帶我回去，為什麼不讓我去死，為什麼留我在這邊？我好髒、好臭、好噁心，我的包包好重，我好痛唷⋯⋯</div>
          <div @click="goToStepQ55" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q5-5 -->
    <div v-if="step===5.5" class="house-0-box">
      <img class="house-0-fishbird" src="@/assets/images/river/river-black-icon.png" alt="ghost">
      <div class="house-0-all-box">
        <div @click="goToStepQ99" class="house-0-all-btn">怪奇也有瘋子啊？算了，趕快帶他回去交差吧。</div>
        <div @click="goToStepQ6" class="house-0-all-btn">這孩子好像因為什麼原因很痛苦⋯⋯安撫他一下好了。</div>
      </div>
    </div>
    <!-- E1 結局 1 Q99  -->
    <div v-if="step===99" class="house-0-box" :style="{ backgroundImage: 'url(' + riverOpacityBg + ')' }">
      <div class="house-0-all-245-box">
       <div class="house-0-245-text">正當你準備收服怪奇時，你發現你無法這麼做。因為必須呼喊怪奇的名字。<br>才能將他收進圖鑑中。而你，根本還不知道他的名字。請先前往<span style="color: #95ff00;">「曙・初見咖啡」</span>，到了之後拿出道具包的<span style="color: #95ff00;">「填字卡」</span>填完內容後，再向店員索取<span style="color: #95ff00;">「現形片」</span>。透過填字卡及透明片，你將得知怪奇的名字。</div>
       <div>怪奇的名字</div>
        <input v-model="riverName" class="house-0-245-input" type="text">
        <div @click="answerRiverName" class="house-0-245-go">GO</div>
      </div>
    </div>
    <!-- Q6 -->
    <!-- Q7 -->
    <!-- Q8 -->
    <!-- Q9 -->
    <!-- Q10 -->
  </div>
</template>

<script>

import riverActualBg from '@/assets/images/river/river-actual-bg.png'
import riverOpacityBg from '@/assets/images/river/river-opacity-bg.png'
export default {
  name: 'RiverHouse',
  components: {

  },
  data () {
    return {
      step: 99,
      riverActualBg: riverActualBg,
      riverOpacityBg: riverOpacityBg,
      isShowQ315: true,
      riverName: ''
    }
  },
  mounted () {
  },
  methods: {
    goToStepQ31 () {
      this.step = 3.1
    },
    goToStepQ315 () {
      this.step = 3.15
    },
    goToStepQ32 () {
      this.step = 3.2
    },
    goToStepQ3 () {
      if (this.power < 3) {
        this.$store.commit('changePower', this.power)
      } else {
        this.$store.commit('changePower', 3)
      }
      this.isShowQ315 = false
      this.step = 3
    },
    goToStepQ4 () {
      this.step = 4
    },
    goToStepQ45 () {
      this.step = 4.5
    },
    goToStepQ99 () {
      // EQ1 結局 1
      this.step = 99
    },
    goToStepQ5 () {
      this.step = 5
    },
    goToStepQ55 () {
      this.step = 5.5
    },
    goToStepQ6 () {
      this.step = 6
    },
    goToStepQ991 () {
      this.step = 99.1
    },
    answerRiverName () {

    }
  },
  computed: {
    power () {
      return this.$store.state.power
    }
  }
}
</script>
<style lang="scss" scoped>

  .river-house {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    .house-0 {

      &-box {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-image: url('~@/assets/images/river/river-weto-bg.png');
      }

      &-fishbird {
        width: 48%;
        height: auto;
        position: absolute;
        top: 4vh;
      }

      &-talk-box {
        display: flex;
        flex-direction: column;
      }

      &-head {
        padding: 4px 16px;
        margin: 220px auto 0px;
        text-align: center;
        background: #95ff00;
        color: black;
        border-radius: 30px;
        z-index: 1;
      }

      &-frame {
        margin-top: 10px;
        max-height: 160px;
        padding: 20px;
        color: white;
        overflow: scroll;
        color: black;
        background: #95ff00;
        z-index: 1;
      }

      &-triangle-box {
        margin-top: 16px;
      }

      &-triangle {
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 15px 15px 0 15px;
        border-color: black transparent transparent transparent;
      }

      // black btn

      &-all-box {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 25vh;
        z-index: 1;
      }

      &-all-btn {
        width: 100%;
        color: white;
        text-align: center;
        background: gray;
        margin-top: 30px;
        padding: 20px 10px;
      }

      // Q8

      &-big-fishbird {
        width: 80%;
        height: auto;
        margin: 50px 0px 20px;
      }

      &-text {
        color: #95ff00;
        text-align: center;
      }

      &-all-box-quick {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 15vh;
        z-index: 1;
      }

      // Q245

      &-all-245-box {
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 5vh;
        background-color: rgba(0, 0, 0, 0.8);
        color: white;
        z-index: 1;
      }

      &-245-fishbird {
        width: 30%;
        height: auto;
      }

      &-245-text {
      margin-top: 18vh;
      padding: 16px;
      }

      &-245-input {
        width: 150px;
        padding: 4px;
        color: black;
        margin-top: 10px;
      }

      &-245-go {
        background-color: #95ff00;
        color: black;
        padding: 6px;
        border-radius: 50%;
        text-align: center;
        margin-top: 10px;
      }

      &-last-frame {
        margin-top: 30vh;
        max-height: 200px;
        overflow: scroll;
        color: white;
        z-index: 1;
        padding: 20px;
      }

    }

  }

</style>



